.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 66px;
  z-index: 1;
  display: flex;
  justify-content: space-between;

  /* Animation */
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav__black {
  background-color: #111;
}
.nav__logo {
  object-fit: contain;
  position: fixed;
  left: 0;
  width: 180px;
}
.nav__avatar {
  width: 30px;
  padding: 28px;
  right: 20px;
  position: fixed;
  object-fit: contain;
}
